.link__list {
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.link a{
  position: relative;
  display: inline-block;
  z-index: 10;
  text-decoration: none;
  color: #495057;
  margin: 0px 0px 10px;
  font-weight: normal;
  font-size: 0.85em;
  letter-spacing: 0.08em;
}
.link a:hover::after{
  width: 109%;
}
.link a::after{
  content: "";
  background: #ccf381;
  height: 0.4em;
  width: 0%;
  display: block;
  margin-top: -0.4rem;
  margin-left: -4%;
  position: absolute;
  z-index: -1;
  transition: width 0.5s ease;
}

@media screen and (max-width: 768px) {
  .link a{
    margin: 0px 0px 6px;
    font-size: 0.65em;
    letter-spacing: 0.07em;
  }
  .link a::after{
    height: 0.4em;
    margin-top: -5.5%;
  }
}
@media screen and (max-width: 425px) {
  .link a{
    margin: 0px 0px 4px;
    font-size: 0.62em;
    letter-spacing: 0.05em;
  }
}